<template> 
  <div>
    Patron<br/>
    {{lastCleaned}}<br/>
    {{timeDiff}}
  </div>
</template>

<script>
import {api} from '@/utils/api'
export default {
  name: 'Patron',
  data () {
    return {
      lastCleaned: null,
      timeDiff: null
    }
  },
  mounted () {
    api.getRaw('gp/test').then( response => {
      this.lastCleaned = response.data.lastCleaned
      this.timeDiff = `Last cleaned ${response.data.timeDiff}` 
      console.log(response)
    }).catch( err => {
      console.log(err)
    }) 
  }
}
</script>

