<template>
  <div class="home">
    <app-head />
    <div :is="type" />
  </div>
</template>

<script>
export default {
  name: 'Home',
  data () {
    return {
      type: null
    }
  },
  mounted () {
    this.type = this.$route.params.type
    if(!this.type) {
      this.type = 'error'
    }
  }
}
</script>
